/* DayPicker styles */
.DayPicker {
  display: inline-block;
  font-size: 1rem;
}
.DayPicker-wrapper {
  position: relative;
  flex-direction: row;
  padding-bottom: 1em;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.DayPicker-Months {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.DayPicker-Month {
  display: table;
  margin: 0 1em;
  margin-top: 1em;
  border-spacing: 0;
  border-collapse: collapse;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.DayPicker-NavButton {
  position: absolute;
  top: 1em;
  right: 1.5em;
  left: auto;
  display: inline-block;
  margin-top: 2px;
  width: 1.25em;
  height: 1.25em;
  background-position: center;
  background-size: 50%;
  background-repeat: no-repeat;
  color: #8B9898;
  cursor: pointer;
}
.DayPicker-NavButton:hover {
  opacity: 0.8;
}
.DayPicker-NavButton--prev {
  margin-right: 1.5em;
  background-image: url('data:image/png;base64,');
}
.DayPicker-NavButton--next {
  background-image: url('data:image/png;base64,');
}
.DayPicker-NavButton--interactionDisabled {
  display: none;
}
.DayPicker-Caption {
  display: table-caption;
  margin-bottom: 0.5em;
  padding: 0 0.5em;
  text-align: left;
}
.DayPicker-Caption > div {
  font-weight: 500;
  font-size: 1.15em;
}
.DayPicker-Weekdays {
  display: table-header-group;
  margin-top: 1em;
}
.DayPicker-WeekdaysRow {
  display: table-row;
}
.DayPicker-Weekday {
  display: table-cell;
  padding: 0.5em;
  color: #8B9898;
  text-align: center;
  font-size: 0.875em;
}
.DayPicker-Weekday abbr[title] {
  border-bottom: none;
  text-decoration: none;
}
.DayPicker-Body {
  display: table-row-group;
}
.DayPicker-Week {
  display: table-row;
}
.DayPicker-Day {
  display: table-cell;
  padding: 0.5em;
  border-radius: 50%;
  vertical-align: middle;
  text-align: center;
  cursor: pointer;
}
.DayPicker-WeekNumber {
  display: table-cell;
  padding: 0.5em;
  min-width: 1em;
  border-right: 1px solid #EAECEC;
  color: #8B9898;
  vertical-align: middle;
  text-align: right;
  font-size: 0.75em;
  cursor: pointer;
}
.DayPicker--interactionDisabled .DayPicker-Day {
  cursor: default;
}
.DayPicker-Footer {
  padding-top: 0.5em;
}
.DayPicker-TodayButton {
  border: none;
  background-color: transparent;
  background-image: none;
  box-shadow: none;
  color: #4A90E2;
  font-size: 0.875em;
  cursor: pointer;
}
/* Default modifiers */
.DayPicker-Day--today {
  color: #D0021B;
  font-weight: 700;
}
.DayPicker-Day--outside {
  color: #8B9898;
  cursor: default;
}
.DayPicker-Day--disabled {
  color: #DCE0E0;
  cursor: default;
  /* background-color: #eff1f1; */
}
/* Example modifiers */
.DayPicker-Day--sunday {
  background-color: #F7F8F8;
}
.DayPicker-Day--sunday:not(.DayPicker-Day--today) {
  color: #DCE0E0;
}
.DayPicker-Day--selected:not(.DayPicker-Day--disabled):not(.DayPicker-Day--outside) {
  position: relative;
  background-color: #4A90E2;
  color: #F0F8FF;
}
.DayPicker-Day--selected:not(.DayPicker-Day--disabled):not(.DayPicker-Day--outside):hover {
  background-color: #51A0FA;
}
.DayPicker:not(.DayPicker--interactionDisabled) .DayPicker-Day:not(.DayPicker-Day--disabled):not(.DayPicker-Day--selected):not(.DayPicker-Day--outside):hover {
  background-color: #F0F8FF;
}
/* DayPickerInput */
.DayPickerInput {
  display: inline-block;
}
.DayPickerInput-OverlayWrapper {
  position: relative;
}
.DayPickerInput-Overlay {
  position: absolute;
  left: 0;
  z-index: 1;
  background: white;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
}
.nav-datovelger {
  position: relative;
  display: inline-block;
  font-size: 1rem;
}
.nav-datovelger * {
  box-sizing: border-box;
}
.nav-datovelger .sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
  white-space: nowrap;
}
.nav-datovelger__inputContainer {
  display: inline-block;
  width: 11rem;
  position: relative;
}
.nav-datovelger__kalenderknapp {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-family: inherit;
  font-size: 1em;
  white-space: nowrap;
  border-radius: 0.125em;
  cursor: pointer;
  user-select: none;
  letter-spacing: 0.0625em;
  line-height: 1.375rem;
  text-decoration: none;
  padding: calc(.475rem - 1px) 2.5rem calc(.525rem - 1px) 2.5rem;
  min-height: 2.5rem;
  vertical-align: middle;
  padding: 0.1rem 0.5rem;
  background-color: #fff;
  border: 1px solid #0067c5;
  border-radius: 0 4px 4px 0;
  margin: 0;
  height: 100%;
  min-height: 2.45rem;
  z-index: 2;
  position: absolute;
  right: 0;
}
.nav-datovelger__kalenderknapp:focus {
  outline: none;
  box-shadow: 0 0 0 3px #00347d;
}
.nav-datovelger__kalenderknapp__icon {
  line-height: 0;
}
.nav-datovelger__kalenderknapp__icon > svg {
  width: 16px;
  height: 16px;
}
.nav-datovelger__kalenderknapp:active,
.nav-datovelger__kalenderknapp:hover,
.nav-datovelger__kalenderknapp:focus {
  outline: none;
}
.nav-datovelger__kalenderknapp:disabled {
  background: transparent;
  cursor: default;
}
.nav-datovelger__caption {
  padding: 0 2rem;
  text-align: center;
  text-transform: capitalize;
  font-size: 2rem;
}
.nav-datovelger__input {
  font-family: "Source Sans Pro", Arial, sans-serif;
  font-size: 1rem;
  font-weight: 400;
  appearance: none;
  padding: 0.5rem;
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid #6a6a6a;
  box-sizing: border-box;
  line-height: 1.375rem;
  width: 100%;
  padding-right: 3rem;
}
.nav-datovelger__input:focus {
  outline: 0;
  box-shadow: 0 0 0 3px #00347d;
}
.nav-datovelger__input:hover {
  border-color: #0067c5;
}
.nav-datovelger__input:disabled,
.nav-datovelger__input[readonly] {
  background-color: #f1f1f1;
  border-color: #6a6a6a;
  box-shadow: none;
  color: #262626;
}
.nav-datovelger__input:disabled:hover,
.nav-datovelger__input[readonly]:hover {
  cursor: not-allowed;
}
.nav-datovelger__input--mini {
  padding: 0.25rem;
}
.nav-datovelger__input:focus {
  outline: 0;
  box-shadow: 0 0 0 3px #00347d;
}
.nav-datovelger__input:hover {
  border-color: #0067c5;
}
.nav-datovelger__input:disabled,
.nav-datovelger__input[readonly] {
  background-color: #f1f1f1;
  border-color: #6a6a6a;
  box-shadow: none;
  color: #262626;
}
.nav-datovelger__input:disabled:hover,
.nav-datovelger__input[readonly]:hover {
  cursor: not-allowed;
}
.nav-datovelger__input--mini {
  padding: 0.25rem;
}
.nav-datovelger__navbar {
  position: absolute;
  left: 0.6rem;
  right: 0.6rem;
  top: 0.4rem;
}
.nav-datovelger__navbar--withYearSelector {
  top: 1rem;
}
.nav-datovelger__navbar__yearSelector {
  text-align: center;
  width: 100%;
  white-space: nowrap;
}
.nav-datovelger__navbar__knapp {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-family: inherit;
  font-size: 1em;
  white-space: nowrap;
  border-radius: 0.125em;
  cursor: pointer;
  user-select: none;
  letter-spacing: 0.0625em;
  line-height: 1.375rem;
  text-decoration: none;
  background-color: #fff;
  padding: calc(.475rem - 1px) 2.5rem calc(.525rem - 1px) 2.5rem;
  min-height: 2.5rem;
  vertical-align: middle;
  padding: 0.5rem 0.5rem;
  position: absolute;
  border: none;
  margin-top: 3px;
}
.nav-datovelger__navbar__knapp:focus {
  outline: none;
  box-shadow: 0 0 0 3px #00347d;
}
.nav-datovelger__navbar__knapp[disabled] {
  cursor: default;
  opacity: 0;
}
.nav-datovelger__navbar__knapp:not([disabled]):hover,
.nav-datovelger__navbar__knapp:not([disabled]):focus,
.nav-datovelger__navbar__knapp:not([disabled]):active {
  outline: 2px solid #00347d;
  border-radius: 3px;
}
.nav-datovelger__navbar__knapp--forrige,
.nav-datovelger__navbar__knapp--previousMonth {
  left: 0;
}
.nav-datovelger__navbar__knapp--neste,
.nav-datovelger__navbar__knapp--nextMonth {
  right: 0;
}
.nav-datovelger__navbar__knapp svg {
  display: block;
}
.nav-datovelger__kalenderPortal {
  position: absolute;
  top: calc(100% + 2px);
  left: 0;
  line-height: 1.3em;
  z-index: 10;
}
.nav-datovelger__kalenderPortal--fullscreen {
  z-index: 100;
  position: fixed;
  display: flex;
  align-items: center;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.8);
}
.nav-datovelger__kalenderPortal--fullscreen .nav-datovelger__kalenderPortal__content {
  margin: 0 auto;
  min-height: 20rem;
}
.nav-datovelger__kalender {
  border: 1px solid #b7b1a9;
  border-radius: 4px;
  overflow: hidden;
}
.nav-datovelger .DayPicker:not(.DayPicker--interactionDisabled) .DayPicker-Day:not(.DayPicker-Day--disabled):not(.DayPicker-Day--outside):active {
  background-color: #0067c5 !important;
  color: #fff;
}
.nav-datovelger .DayPicker:not(.DayPicker--interactionDisabled) .DayPicker-Day:not(.DayPicker-Day--disabled):not(.DayPicker-Day--outside):hover {
  z-index: 2;
  position: relative;
  border-radius: 2px !important;
  box-shadow: none;
  outline: 3px solid #00347d;
}
.nav-datovelger .DayPicker:not(.DayPicker--interactionDisabled) .DayPicker-Day:not(.DayPicker-Day--disabled):not(.DayPicker-Day--outside):hover:not(.DayPicker-Day--disabled):not(.DayPicker-Day--selected):not(.DayPicker-Day--outside) {
  background-color: inherit;
}
.nav-datovelger .DayPicker:not(.DayPicker--interactionDisabled) .DayPicker-Day:not(.DayPicker-Day--disabled):not(.DayPicker-Day--outside):active,
.nav-datovelger .DayPicker:not(.DayPicker--interactionDisabled) .DayPicker-Day:not(.DayPicker-Day--disabled):not(.DayPicker-Day--outside):focus {
  z-index: 2;
  position: relative;
  border-radius: 2px !important;
  background: #fff;
  z-index: 3;
  box-shadow: none;
  outline: 3px solid #00347d;
}
.nav-datovelger .DayPicker:not(.DayPicker--interactionDisabled) .DayPicker-Day.DayPicker-Day--disabled:focus,
.nav-datovelger .DayPicker:not(.DayPicker--interactionDisabled) .DayPicker-Day .DayPicker-Day--outside:focus {
  outline: 2px solid #00347d;
  border-radius: 3px;
  z-index: 2;
  position: relative;
  border-radius: 2px !important;
}
.nav-datovelger .DayPicker {
  background: #fff;
}
.nav-datovelger .DayPicker-wrapper:focus {
  outline: 2px solid #00347d;
  border-radius: 3px;
}
.nav-datovelger .DayPicker-Day {
  border-radius: 0;
  border-radius: 0 !important;
  padding-left: 0.6em;
  padding-right: 0.6em;
}
.nav-datovelger .DayPicker-Day--selected:not(.DayPicker-Day--disabled) {
  color: #fff;
  background-color: #0067c5 !important;
}
.nav-datovelger .DayPicker-Day--disabled {
  color: #4f4f4f;
  background-color: #f1f1f1;
  font-weight: normal !important;
  position: relative;
  text-decoration: line-through;
}
.nav-datovelger .DayPicker-Day--outside {
  background-color: transparent;
}
.nav-datovelger .DayPicker-Day--today {
  color: #0067c5;
}
.nav-datovelger .DayPicker-Day--today.DayPicker-Day--disabled {
  color: #66a5f4;
}
.nav-datovelger .DayPicker-Day:last-child {
  color: #c30000 !important;
  font-style: italic;
}
.nav-datovelger .DayPicker-Day--isPublicHoliday:not(.DayPicker-Day--selected) {
  color: #c30000 !important;
  font-style: italic;
}
.nav-datovelger .DayPicker-Day--selected:last-child {
  color: white !important;
}
.nav-datovelger .DayPicker-Weekday {
  color: #4f4f4f;
  text-transform: capitalize;
  font-size: 1rem;
  font-style: italic;
}
.nav-datovelger .DayPicker-Caption {
  padding: 0 2rem;
  text-align: center;
  text-transform: capitalize;
}
.nav-datovelger .DayPicker-WeekNumber {
  border-right: none;
  position: relative;
  padding-left: 0;
  padding-right: 1rem;
  font-size: 1rem;
  color: #4f4f4f;
  font-style: italic;
}
.nav-datovelger .DayPicker-WeekNumber:focus {
  outline: none;
  position: relative;
}
.nav-datovelger .DayPicker-WeekNumber:focus:after {
  content: '';
  position: absolute;
  top: 2px;
  left: -0.5rem;
  bottom: 2px;
  right: 0.55rem;
  border: 1px solid #00347d;
  border-radius: 2px;
}
.nav-datovelger .DayPicker-WeekNumber:before {
  content: '';
  width: 1px;
  height: 100%;
  top: 0;
  right: 0.5rem;
  background-color: #c9c9c9;
  position: absolute;
}
.nav-datovelger .DayPicker--range .DayPicker-Day--selected {
  background-color: #3386e0 !important;
}
.nav-datovelger .DayPicker--range .DayPicker-Day--start {
  background-color: #0067c5 !important;
}
.nav-datovelger .DayPicker--range .DayPicker-Day--end {
  background-color: #0067c5 !important;
}
.nav-datovelger .DayPicker--range .DayPicker-Day--outside {
  visibility: hidden;
}
.nav-datovelger__periode {
  display: flex;
  padding-right: 2.5rem;
}
.nav-datovelger__periode .nav-datovelger__inputContainer {
  width: 9rem;
}
.nav-datovelger__periode__startInput {
  padding-right: 1.5rem;
}
.nav-datovelger__periode__startInput:after {
  content: '';
  margin-left: 0.35rem;
  line-height: 2rem;
  position: absolute;
}
.nav-datovelger__yearSelector .selectContainer:first-child {
  margin-right: 0.25rem;
}
.nav-datovelger__yearSelector .selectContainer--monthOnly {
  width: 70%;
}
.nav-datovelger__yearSelector .skjemaelement__input {
  font-size: 1rem;
}
.nav-datovelger__yearSelector .skjemaelement__input--year {
  padding-right: 2.5rem;
}
